$main-color: #ec407a;
$academic-font: "Garamond", serif;

@media (prefers-color-scheme: dark){
    .dark-mode{
    background: #333;
    color:white;
    .invertable{
        filter:invert(1) hue-rotate(180deg);
    }

    .img-board:hover{
        .cover{
            opacity: 0;
        }
    }
    .profile{
        background-color: #444;
        .zh_name{
            filter: invert(1);
        }
        .img-board{
            width: 150px;
            height: 150px;
            margin:20px auto;
            .cover{opacity: 0;}
            *{border-radius: 10px;opacity: 1;border:none;}
        }
        .dot::before{
            content: "";
            background: white;
        }
        .line::before{
            content: "";
            background: #666;
        }
    }

    ul{
        list-style: none;
    }
    li{
        background-color: #444;
    }


    .education{
        li{
            .duration{color: #888;}
            .remark-b{background-color: #666;color:#AAA}
        }
    }

    .paper{
        .remark{
            color: #888;
        }
    }
    .tolight{
        display: block;
    }
    .todark{
        display: none;
    }
}
}
